.auth-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #f9f9f9;
  text-align: center;
}

.signup-form {
  display: flex;
  flex-direction: column;
}

.signup-form h2 {
  margin-bottom: 15px;
  text-align: center;
}

.signup-form label {
  margin: 10px 0 5px;
  display: block;
  text-align: left;
}

.signup-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.signup-form button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

.signup-form button:hover {
  background-color: #0056b3;
}

.message {
  margin-top: 10px;
  font-size: 0.9rem;
  color: green;
}

.redirect-text {
  margin-top: 20px;
  font-size: 0.9rem;
  color: #555;
}

.login-redirect-button {
  background: none;
  color: #007bff;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  font-weight: bold;
}

.login-redirect-button:hover {
  color: #0056b3;
}
