.hero {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Vertically center content */
  align-items: center; /* Horizontally center content */
  height: 100vh; /* Full viewport height */
  padding-top: 10vh; /* Adjust padding to move content as needed */
  background-image: url('../images/home.jpg'); /* Ensure the path is correct */
  background-size: cover;
  background-position: center;
  color: #f4f4f4; /* Soft light gray for elegance and readability */
  text-shadow: 1px 2px 4px rgba(0, 0, 0, 0.8); /* Stronger text contrast for readability */
}

.hero h2 {
  font-size: 48px; /* Large text for the main headline */
  margin-bottom: 20px; /* Space between the headline and the subtext */
  color: #ffffff; /* Pure white for the headline to stand out */
}

.hero p {
  font-size: 24px; /* Subtext size */
  max-width: 60%; /* Limits width of the paragraph for better readability */
  text-align: center; /* Ensures text is centered */
  color: #eaeaea; /* Slightly darker shade of white for subtext for subtle distinction */
}

.hero .btn {
  background-color: #556270; /* Subdued blue-gray, professional and modern */
  color: #ffffff; /* White text for contrast and visibility */
  padding: 12px 30px;
  border-radius: 5px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effects */
}

.hero .btn:hover {
  background-color: #404b57; /* Darker shade on hover for a sophisticated look */
  color: #ccd1d1; /* Soft light gray on hover for a gentle contrast */
}
