/* Add a professional background color to the entire menu section */
.menu {
  padding: 2rem;
  background: linear-gradient(to bottom, #f7f7f7, #e9ecef); /* Subtle gradient */
  min-height: 100vh; /* Ensure the page fills the viewport */
}

/* Styling for each category */
.category {
  margin: 2rem 0;
  background: #ffffff; /* White background for category containers */
  border-radius: 12px; /* Larger rounded corners for a modern look */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  padding: 1.5rem;
}

/* Styling for category headings */
.category h3 {
  font-size: 1.8rem;
  color: #333;
  text-transform: capitalize;
  margin-bottom: 1.5rem; /* Space below heading */
  text-align: center; /* Center-align the category heading */
}

/* Styling for the list of menu items */
.category ul {
  list-style: none; /* Remove default list styles */
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-evenly; /* Ensures items are spaced evenly */
}

/* Styling for individual menu items */
.menu-item {
  text-align: center;
  width: calc(25% - 20px); /* Adjusts to fit 4 items per row with gap */
  max-width: 250px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f8f9fa; /* Light gray background for individual menu cards */
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  padding: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth animation */
}

/* Styling for menu item images */
.menu-image {
  width: 100%;
  height: auto;
  aspect-ratio: 1 / 1; /* Ensures images maintain a square ratio */
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.8rem;
}

/* Styling for menu item names */
.menu-item p {
  font-size: 1.1rem;
  color: #555;
  margin: 0;
}

/* Hover effects for interactivity */
.menu-item:hover {
  transform: scale(1.05); /* Slight zoom effect */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Stronger shadow on hover */
}

/* Optional media queries for responsiveness */
@media (max-width: 768px) {
  .menu-item {
    width: calc(50% - 20px); /* Adjust to 2 items per row */
  }
}

@media (max-width: 480px) {
  .menu-item {
    width: calc(100% - 20px); /* Adjust to 1 item per row */
  }
}
